button {
  background-color: #4CAF50;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #3e8e41;
}
.container {
  display: flex;
  flex-direction: column; /*  Размещаем стикеры один под другим */
  align-items: center;
  padding: 20px; /* добавляем отступы */
  background-color: #f0f0f0;
  min-height: 100vh; /* занимает всю высоту экрана */
  box-sizing: border-box; /* Учитываем padding при расчете высоты */
}

.sticker {
  background-color: lightgrey;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  transition: box-shadow 0.3s ease;
  width: 80%; /* Задаём ширину стикера */
  max-width: 600px; /* Ограничиваем максимальную ширину */
  margin-bottom: 15px;  /* Добавляем отступ между стикерами */
  box-sizing: border-box; /* Важно, чтобы padding не увеличивал общую ширину */
}

.sticker h2 {
  margin-bottom: 5px;
}

.sticker textarea {
  width: 100%;
  height: 100px;
  padding: 10px;
  border: 4px solid #ccc;
  border-radius: 15px;
  margin-bottom: 10px;
  font-size: 16px;
  box-sizing: border-box;
}


